import { Component, OnInit } from '@angular/core';
import axios from 'axios';
import { api, munaApi, munaPublicKey, OnlineIp } from '../server';
import { ActivatedRoute } from '@angular/router'
import {Storage} from '@ionic/storage-angular';
import * as moment from 'moment';
import { AlertController, LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-patient-test',
  templateUrl: './patient-test.page.html',
  styleUrls: ['./patient-test.page.scss'],
})
export class PatientTestPage implements OnInit {

  constructor(
    public route : ActivatedRoute,
    public storage: Storage,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
  ) { }

  patientId = 0;
  patient = {
    munaId: null,
    name: '',
    idPatient: 0
  };
  tests = [];
  user = null;
  patientTests = [];

  ngOnInit() {

    this.storage.get('user').then(data => {
      this.user = data
      console.log(this.user);
      
    })
    
    this.route.paramMap.subscribe(param => {
      this.patientId = param['params']['id'];
      axios.get(api + 'patient/' + this.patientId).then(res => {
        this.patient = res.data;
        console.log(this.patient);
        
      });
      axios.get(api + 'tests').then(res => {
        this.tests = res.data;
        console.log(this.tests);
        
      });
      this.loadPatientTest();
    })
    
  }

  addNewCard() {
    axios.post(api + 'addPatientTest', {
      patientId: this.patientId,
      createdBy: this.user['data']['idUser']
    }).then(res => {
      axios.get(api + 'patientTestId/' + this.patientId).then(res => {
        this.patientTests = res.data
        console.log(this.tests);
      })
    })
  }


  fixDate(date) {
    return moment(date).format('YYYY-MM-DD –– hh:mm A')
  }

  async addNewTest(id,test) {
   
    
    let loading = await this.loadingCtrl.create({
      message: 'Please wait...'
    });
    await loading.present();
    if (test == undefined) return false;

    axios.post(api + 'addPatientResult' , {
      testId: test['idTest'],
      patientTestId: id,
      requested: moment().format('YYYY-MM-DD HH:mm:ss'),
      collected: moment().format('YYYY-MM-DD HH:mm:ss'),
      result: '',
      quantity: 1,
      price: test['price'],
    }).then(async res => {
      this.loadPatientTest();
    }).finally(async () => {
      await loading.dismiss();
    })
  }


  async loadPatientTest() {
    let loading = await this.loadingCtrl.create({
      message: 'Please wait...'
    });
    await loading.present();
    axios.get(api + 'patientTestId/' + this.patientId).then(res => {
      this.patientTests = res.data
    }).finally(async () => {
      await loading.dismiss();
    });
  }

  getTestName(testId) {
    let test = this.tests.filter(x => x.idTest == testId)[0];
    return test['testName']
  }

  getTestOptions(testId) {
    let test = this.tests.filter(x => x.idTest == testId)[0];
    return test['options']
  }

  isTestFixed(testId) {
    let test = this.tests.filter(x => x.idTest == testId)[0];
    return test['fixed']
  }

  async setResult(option,testId,test) {
    let loading = await this.loadingCtrl.create({
      message: 'Loading...'
    });
    await loading.present();
    test.result = option.detail.value
    test.perform = moment().format('YYYY-MM-DD HH:mm:ss');
    axios.put(api + 'patientResult/' + testId, test).then((res) => {
      console.log('done');
      location.reload();
    })
    
  }

  async deleteCard(id) {
    let message = await this.alertCtrl.create({
      header: 'Are you sure?',
      subHeader: 'you cannot undo this action.',
      buttons: [
        {
          text: "DELETE",
          role: "cancel",
          handler: () => {
            axios.delete(api + 'patientTest/' + id).then(res => {
              this.loadPatientTest();
            })
          }
        },
        {
          text: "Cancel"
        }
      ]
    });
    await message.present();
  }



  async print(result,celDate,testDate,resultId,muna = 0) {
    

    console.log(celDate,testDate);
    celDate = new Date(celDate)
    celDate.setHours(0)
    celDate.setMinutes(0)
    celDate.setSeconds(0)
    celDate = celDate.toISOString();
    testDate = new Date(testDate)
    testDate.setHours(0)
    testDate.setMinutes(0)
    testDate.setSeconds(0)
    testDate = testDate.toISOString();
    


    console.log(testDate);
      let loading = await this.loadingCtrl.create({
        message: 'Please wait...'
      });
      await loading.present();

      // PREPARE PDF
      let gender = 'Male';
      if (this.patient['gender'] == 1) {
        gender = "Female"
      }
      let age = moment().diff(this.patient['dob'], 'years',false);
      axios.post(api + 'certificateResult', {
        "name": this.patient['name'],
        "enName":this.patient['enName'],
        "gender": gender,
        "age": age,
        "patientResultId": resultId,
        "documentId": this.patient['documentId'],
        "date": moment(testDate).format('YYYY-MMMM-DD'),
        "nationality": this.patient['nationalName'],
        "result":result.replace(/ /g,''),
        "patientPhoto":this.patient['image']['photoPath']
    }).then(res => {
      let pdf = res.data;
      if (muna == 0) {
        window.open(api + pdf.certificatePath)
        loading.dismiss();
      } else {
        // CHECK IF PATIENT ALREADY HAD A CERTIFICATE;
      let munaId = this.patient['munaId'];
      axios.get('http://192.168.0.254:5555/integrations/test-results?filter[participant]=' + munaId).then(res => {
        // TOTAL CERTIFICATES
        if (res.data.data.length != 10019200991) {
          // NO CERT
          loading.message = 'Creating MUNA blobs...';
          let sex = 'Male';
          if (this.patient['gender'] == 1) {
            sex = 'Female';
          }
          axios.post(munaApi + 'blobs', {
            "data": {
              "type": "kyc_form",
              "attributes": {
                "value": `{\"first_name\": \"${this.patient['enName']}\",\"last_name\": \"\",\"birthday_date\": \"${this.patient['dob']}\",\"gender\": \"${sex}\",\"phone_number\": \"${this.patient['phone']}\",\"address\": \"-\",\"documents\": {\"kyc_avatar\": {\"mime_type\": \"\",\"name\": \"\",\"content\": \"\"}}}`
              },
              "relationships": {
                "owner": {
                  "data": {
                    "id": munaPublicKey
                  }
                }
              }
            }
          }          
          ).then(res => {
            let blobId = res.data.data.id;
            console.log(res.data);
            loading.message = 'MUNA account role updating...';
            axios.post(munaApi + 'integrations/hgate/change_role_requests',{
              "data": {
              "destination": munaId, 
              "account_role": "participant",					
              "creator_details": { 
                      "blob_id": blobId 
                  }
                }
              },
              {
                headers: { 
                  'Content-Type': 'application/json'
                }
              }
              ).finally(() => {
                console.log('CHANGE ROLE REQUEST',{
                  "data": {
                  "destination": munaId, 
                  "account_role": "participant",					
                  "creator_details": { 
                          "blob_id": blobId 
                      }
                    }
                  });
                
                // SUBMIT RESULT
                loading.message = 'UPLOADING...'
                axios.post(munaApi + 'integrations/hgate/tests', {
                  "data": {
                    "participant": munaId,
                    "manufacturer_of_prc_device": "COP",
                    "serial_number_of_device": "31433",
                    "manufacturer_of_reagent": "regat",
                    "calibration_date": celDate,
                    "technician_or_physician_name": "Dr Wissam",
                    "number_of_cycles": "50",
                    "test_result": result.replace(/ /g,''),
                    "test_name": "COVID PCR",
                    "test_subtype_id": "test1",
                    "custom_patient_id": this.patient['idPatient'].toString(),
                    "test_type_id": "test0",
                    "test_date": testDate,
                    "documents": {
                      "patient_photo": {
                        "content": pdf.photo,
                        "name": pdf.photoName,
                        "mime_type": pdf.photo_mime
                      },
                      "additional_files": {
                        "content": pdf.pdf,
                        "name": pdf.fileName,
                        "mime_type": pdf.mime_type
                      },
                      "pcr_raw_machine_data": {
                        "content": "",
                        "name": "",
                        "mime_type": ""
                      }
                    },
                    "comments": "no comment"
                  }
                }).catch(err => {
                  console.log(err);
                  console.log({
                    "data": {
                      "participant": munaId,
                      "manufacturer_of_prc_device": "COP",
                      "serial_number_of_device": "31433",
                      "manufacturer_of_reagent": "regat",
                      "calibration_date": celDate,
                      "technician_or_physician_name": "Dr Wissam",
                      "number_of_cycles": "50",
                      "test_result": result.replace(/ /g,''),
                      "test_name": "COVID PCR",
                      "test_subtype_id": "test1",
                      "custom_patient_id": this.patient['idPatient'].toString(),
                      "test_type_id": "test0",
                      "test_date": testDate,
                      "documents": {
                        "patient_photo": {
                          "content": pdf.photo,
                          "name": pdf.photoName,
                          "mime_type": pdf.photo_mime
                        },
                        "additional_files": {
                          "content": pdf.pdf,
                          "name": pdf.fileName,
                          "mime_type": pdf.mime_type
                        },
                        "pcr_raw_machine_data": {
                          "content": "",
                          "name": "",
                          "mime_type": ""
                        }
                      },
                      "comments": "no comment"
                    }
                  });
                  
                }).finally(() => {
                  // CHECK
                  axios.get('http://192.168.0.254:5555/integrations/test-results?filter[participant]=' + munaId).then(res => {
                    if (res.data.data.length > 0) {
                      let certId = res.data.data[0].id; // GET LAST RESULT
                      loading.message = 'Downloading MUNA certificate...';
                      axios.get(`http://192.168.0.254:5555/integrations/test-results/${certId}/certificate`).then(res => {
                        console.log(res.data);
                        window.open(`http://192.168.0.254:5555/integrations/test-results/${certId}/certificate`);
                        loading.dismiss();
                      })
                    }
                  });
                })
              })
          }).catch(async err => {
            await loading.dismiss();
            console.log(err);
          })
        } else {
          // CERT FOUND
          // JUST PRINT IT
            let certId = res.data.data[res.data.data.length - 1].id; // GET LAST RESULT
            loading.message = 'Downloading MUNA certificate...';
            axios.get(`http://192.168.0.254:5555/integrations/test-results/${certId}/certificate`).then(res => {
              console.log(res.data);
              window.open(`http://192.168.0.254:5555/integrations/test-results/${certId}/certificate`);
              loading.dismiss();
            })
        }
        
      });
      }

      
    });

      
          

  }

  setRecieveDate(id,test,e) {
    let date = new Date(e.detail.value);
    date.setDate(date.getDate());
    test.received = moment(date).format('YYYY-MM-DD HH:mm:ss');
    axios.put(api + 'patientResult/' + id, test).then((res) => {
      console.log('done');
      test.received = moment(e.detail.value).format('YYYY-MM-DD HH:mm:ss');
    })
  }

  printInvoice(test) {
    axios.post(api + 'createBill', {
      "name" : this.patient['name'],
      "age" : moment().diff(this.patient['dob'], 'years',false) + 'Years – ' + this.patient['gender'],
      "received" : moment(test.received).format('YYYY-MM-DD'),
      "phone" : this.patient['phone'],
      "patientId" : this.patient['idPatient'],
      "billId" : test['idPatientResult'],
      "billDate" : moment().format('YYYY-MMMM-DD'),
      "totalPrice": test.price,
      "tests" :[
          {"testName" : this.getTestName(test.testId) , "charge" : test.price}
      ]
    }).then(res => {
        console.log(res.data.url);
        
        setTimeout(() => {
          window.open(api + res.data.url + '?' + Math.random())
        }, 1000);
    })
  }

  getBarcode(id) {
    let num = 13;
    let idNum = id;
    idNum = idNum.toString().length
    let remain = num - idNum;
    let x = ''
    for (let i = 0; i < remain; i++) {
      x = x + '0'
    }
    return x + id.toString()
  }


  async genQR(result) {
    
    let loading = await this.loadingCtrl.create({
      message: 'Wait...'
    });
    await loading.present();
    axios.get(api + 'uploadCertificate/' + result).then(res => {
      console.log(res.data);
      
    }).finally(async () => {
      await loading.dismiss();
    })
  }
}
